import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ChartingComponent } from './components/charting/charting.component';
import { PageChartingComponent } from './page-charting.component';
import { ChartModule } from 'angular2-chartjs';
import { ChartjsLineComponent } from './components/chartjs-line/chartjs-line.component';
import { ChartjsBarComponent } from './components/chartjs-bar/chartjs-bar.component';
import { NgxchartsBarComponent } from './components/ngxcharts-bar/ngxcharts-bar.component';
import { NgxchartsLineComponent } from './components/ngxcharts-line/ngxcharts-line.component';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { HighchartsComponent } from './components/highcharts/highcharts.component';
import { HighchartsChartModule } from 'highcharts-angular';


@NgModule({
  imports: [
    CommonModule,
    ChartModule,
    NgxChartsModule,
    HighchartsChartModule
  ],
  declarations: [
    ChartingComponent,
    PageChartingComponent,
    ChartjsLineComponent,
    ChartjsBarComponent,
    NgxchartsBarComponent,
    NgxchartsLineComponent,
    HighchartsComponent
  ]
})
export class ChartingModule { }
