import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import * as Highcharts from 'highcharts';

@Component({
  selector: 'glo-highcharts',
  templateUrl: './highcharts.component.html',
  styleUrls: ['./highcharts.component.css']
})
export class HighchartsComponent implements OnInit, OnChanges {

  Highcharts = Highcharts; // required
  chartConstructor = 'chart'; // optional string, defaults to 'chart'
  chartOptions: any = {
    series: [{
      type: 'line',
      data: [
        ['January', 65],
        ['February', 59],
        ['March', 80],
        ['April', 81],
        ['May', 56],
        ['June', 55]
      ],
      name: 'Water Savings'
    }],
    xAxis: {
      type: 'category'
    },
    yAxis: {
      title: {
        text: 'litres'
      }
    },
    title: {
      text: 'Monthly Report'
    }
  }; // required
  @Input() barSeries: any[] = [];
  updateFlag = false; // optional boolean
  oneToOneFlag = true; // optional boolean, defaults to false

  @Input() newChartOptions: any = {};

  constructor() { }

  ngOnInit() {
    // setTimeout(() => {
    //   this.chartOptions.series[0].name = 'Electricity';
    //   this.chartOptions.title.text = 'Weekly rep';
    //   this.updateFlag = true;
    // }, 2000);
  }

  ngOnChanges(changes: SimpleChanges) {
    console.log(changes);
    Object.assign(this.chartOptions, this.newChartOptions);
    // this.chartOptions = this.newChartOptions;
    // this.chartOptions.series.push(this.barSeries[0]);
    this.updateFlag = true;
  }

}
