import { Injectable } from '@angular/core';
import {
  HttpClient, HttpHeaders, HttpInterceptor, HttpRequest, HttpHandler, HttpEvent,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

export interface LoginRequest {
  email: string;
  password: string;
}

export interface LoginResponse {
  _id: string;
  updatedAt: string;
  createdAt: string;
  name: string;
  email: string;
  userType: string;
  __v: string;
}

@Injectable({
  providedIn: 'root'
})
export class DataService implements HttpInterceptor {
  private static HEADERS = new HttpHeaders({ 'Content-Type': 'application/json' });
  public cookies: string[];

  constructor(private http: HttpClient) { }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    request = request.clone({
      withCredentials: true,
      headers: DataService.HEADERS
    });

    return next.handle(request).pipe(
      catchError((err: HttpErrorResponse) => {
        if (err instanceof HttpErrorResponse) {
          if (err.status === 401) {
            // redirect to the login route
            // or show a modal
          }
          return throwError('User not authenticated');
        }
        return throwError(err);
      })
    );
  }

  postLogin(request: LoginRequest): Observable<LoginResponse> {
    return this.http.post<LoginResponse>(environment.BASE_URL + '/login', request, {
      headers: DataService.HEADERS
    });
  }
}
