import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'glo-charting',
  templateUrl: './charting.component.html',
  styleUrls: ['./charting.component.css']
})
export class ChartingComponent implements OnInit {

  lineSeries = [{
    type: 'line',
    data: [75, 69, 90, 91, 66, 65, 50],
    name: 'Shop 1'
  }];

  barSeries = [{
    type: 'bar',
    data: [
      ['January', 55],
      ['February', 49],
      ['March', 70],
      ['April', 71],
      ['May', 46],
      ['June', 45],
      ['July', 30]
      ],
    name: 'Shop 2'
  }];

  highchartsLineOptions = {
    chart: {
      type: 'line'
    },
    series: this.lineSeries,
    xAxis: {
      type: 'category',
      categories: ['January', 'February', 'March', 'April', 'May', 'June', 'July']
    },
    yAxis: {
      title: {
        text: 'kWh'
      }
    },
    title: {
      text: 'Electricity Savings'
    }
  };

  highchartsBarOptions = {
    chart: {
      type: 'bar'
    },
    series: this.barSeries,
    xAxis: {
      type: 'category'
    },
    yAxis: {
      title: {
        text: 'kWh'
      }
    },
    title: {
      text: 'Electricity Savings'
    }
  };


  constructor() { }

  ngOnInit() {
  }

}
